import React, { Component } from "react";
import './NavBar.css';
import './Grid.css';
import Login from './login';
import NameForm from './Signup.js';
import { BrowserRouter, Switch, Route, Link } from "react-router-dom";














class NavBar extends Component{


render() {
	return(

<div className='Nav row'> 
         
         <div className='col-6'>
         <div className='logo'>
             IntWiff
	    </div>
	    </div>
	    <div className='col-6'>
	     <div className='nav-links-box'>
	       <BrowserRouter>
	     <Link
            to={{
              pathname: "/Login",
              
            }}
            className="nav-link"
          >
            Login
          </Link>

          <Link
            to={{
              pathname: "/SignUp",
              
            }}
            className="nav-link"
          >
            SignUp
          </Link>
 
           <Switch>
            
            <Route path="/Login" component={Login} />
            <Route path="/SignUp" component={NameForm} />

          </Switch>
          </BrowserRouter>
        
        </div>
       
        </div>
    
</div>

		)
}
}


export default NavBar;
