import React, { Component } from 'react';
import Dialog from '@material-ui/core/Dialog';
import TextField from '@material-ui/core/TextField';
import './login.css';
import { useHistory } from "react-router-dom";



function Login(props) {
  const [open, setOpen] = React.useState(true);
   let history = useHistory();
  const handleClose = () => {
    setOpen(false);
    history.goBack();

    

  };



  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
       
      >
      <div className='container'>
      <div className='LoginTitle'>
     <center> Sign In </center></div>
         <div className='LoginBody'>
           <form autoComplete="on">
     <div className='EOP'> <TextField id="eop" label="Email or Mobile" variant="outlined" fullWidth /></div>
      <div className='PASS'><TextField id="pass" label="Password" variant="outlined" type='Password' fullWidth /></div>
       <center>
          <button type='submit' onClick={handleClose} className='btn-login'>
            Login
          </button>
          </center>
    </form>
      
     
       
          </div>
          </div>
      </Dialog>

    </div>
  );
}
export default Login;