class NameForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {value: ''};
    this.state ={'open':true};
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
	
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {
    alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
  }

  handleClose(){
  	this.setState({'open':false});
  	
  }

  render() {


    return (
    
               	<div className='dialog'>
               	 <Dialog
                       open={this.state.open}
                       onClose={() => this.handleClose}
                       fullWidth
                       fullScreen

                     >
                    <div className='container signupbox'>
                     <div className='row'>
                     <div className='col-6 leftbox'>
                     <div className='info'>
                        <div className='infohead'>INTWIFF </div>
                        <div className='infobody'> Rent Your Ride On The Go</div>
                     </div>
                       <div className='footer'>IntWiff ©2020 </div>
                     </div>
                     <div className='col-6 rightbox'>
                     <div className='SignUpCont'>
                    <div className='signupheader'> Register Now</div>
                           <form autoComplete="on">
                    <div className='Mobile'> <TextField id="phone" label="Mobile" variant="outlined" fullWidth onChange={this.handleChange} /></div>
                   
                      <center>
                         <button type='submit' className='btn-login'>
                          Send OTP
                         </button>
                         </center>
                   </form>
                    <div className='OTP'> <TextField id="otp" label="OPT" variant="outlined" fullWidth type='tel' /></div>

                     </div>
                     </div>


                     </div>
                    </div>


                     </Dialog>
               	</div>





    );
  }
}

export default NameForm;